<style scoped lang="less">
</style>
<template>
  <div class="pendingYYFrozen content-layout">
    <div class="content-header">
      <div class="content-btn">
        <el-button type="info" size="small" @click="addYYFrozenFn">新建冰冻</el-button>
      </div>
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="a" label="姓名"></el-table-column>
        <el-table-column prop="b" label="性别"></el-table-column>
        <el-table-column prop="c" label="年龄"></el-table-column>
        <el-table-column prop="d" label="送检单位"></el-table-column>
        <el-table-column prop="e" label="手术部位"></el-table-column>
        <el-table-column prop="f" label="冰冻时间段" width="300"></el-table-column>
        <el-table-column label="操作" width="200">
          <template scope="scope">
            <el-button type="text" size="small" @click.native.prevent="lookYYFrozenFn(scope.$index, tableData)">查看</el-button>
            <el-button type="text" size="small" @click.native.prevent="editYYFrozenFn(scope.$index, tableData)">编辑</el-button>
            <el-button type="text" size="small" @click.native.prevent="submitYYFrozenFn(scope.$index, tableData)">提交</el-button>
            <el-button type="text" size="small" @click.native.prevent="deleteYYFrozenFn(scope.$index, tableData)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <add-frozen v-show="showaddFrozen" :recordData="FrozenData"  @getChildMessage="closeaddFrozen"></add-frozen>
    </transition>
    <transition name="el-fade-in">
      <look-frozen v-show="showlookFrozen" :recordData="FrozenData"  @getChildMessage="closelookFrozen"></look-frozen>
    </transition>
  </div>
</template>
<script>
  import headerSearch from '../../../public/header-search'
  import addFrozen from '../../../public/addFrozen'
  import lookFrozen from '../../../public/lookFrozen'
  import searchDataModule from '../../../../api/searchDataModule'
  import searchInitDefault from '../../../../api/searchInitDefault'
  export default {
    name: '',
    data(){
      return {
        seachData: {
          starttime: searchDataModule.starttime,
          endtime: searchDataModule.endtime,
          xbval: searchDataModule.xbval,
          xb: searchDataModule.xb,
          seachInfo: searchDataModule.seachInfo
        },
        tableData: [
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'},
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'},
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'},
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'},
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'},
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'},
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'},
          {a: '李四', b: '男', c: '90', d: '妇科', e: '腹部', f: '2017-06-12  12:00 -- 2017-06-12  14:30'}
        ],
        currentPage: 1,//分页
        FrozenData: {},
        showaddFrozen: false,
        showlookFrozen: false,
      }
    },
    props: ['a'],
    components: {
      headerSearch,
      addFrozen,
      lookFrozen
    },
    mounted(){
      searchInitDefault(searchDataModule);
    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },

      seachFn(val){
        console.log(this.seachData)
      },
      /*新建冰冻*/
      addYYFrozenFn(){
        this.showaddFrozen = true
      },
      /*编辑*/
      editYYFrozenFn(i,arr){
        this.showaddFrozen = true
      },
      /*退出添加修改*/
      closeaddFrozen(){
        this.showaddFrozen = false
      },
      /*查看*/
      lookYYFrozenFn(i,arr){
        this.showlookFrozen = true;
      },
      /*退出查看*/
      closelookFrozen(){
        this.showlookFrozen = false
      },
      /*提交*/
      submitYYFrozenFn(i,arr){
        this.$confirm('此操作将提交病历, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
//          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '提交成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消提交'
          });
        });
      },
      /*删除*/
      deleteYYFrozenFn(i,arr){
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
    }
  }
</script>



